<script>
import axios from "axios";

export default {
  name: 'App',
  created() {
    this.flush()
  },
  data() {
    return {
      url: '/api',
      indexCnt: 0,
      totalCnt: 0,
      curImage: null,
      matchImageList: null,
      onLoad: false,
      info: {
        tp5_ac: null,
        tp5_rc: null,
        tp10_ac: null,
        tp10_rc: null,
        p50_ac: null,
        p50_rc: null
      }
    }
  },
  components: {},
  methods: {
    flush() {
      axios.get(this.url + '/num')
          .then(res => this.indexCnt = res.data.data)
          .catch(err => console.log(err))

      axios.get(this.url + '/tot_num')
          .then(res => this.totalCnt = res.data.data)
          .catch(err => console.log(err))
    },
    upload() {
      const files = this.$refs.up.files
      const config = {
        headers: {'Content-Type': 'multipart/form-data'}
      };
      for (let fileKey = 0; fileKey < files.length; ++fileKey) {
        const formData = new FormData()
        formData.append('file', files[fileKey])

        axios.post(this.url + '/upload', formData, config)
            .then(() => this.flush())
            .catch(err => console.log(err))
      }
    },
    find() {
      const file = this.$refs.fi.files[0]
      const config = {
        headers: {'Content-Type': 'multipart/form-data'}
      };
      const formData = new FormData()
      formData.append('file', file)
      formData.append('file', file)

      axios.post(this.url + '/find', formData, config)
          .then(res => {
            this.curImage = file.name
            this.info = res.data.data.info
            this.matchImageList = res.data.data.data
            console.log(this.matchImageList)
          })
          .catch(err => console.log(err))
    },
    reduce() {
      this.onLoad = true
      axios.post(this.url + '/reduce', {word: 64})
          .then(() => {
            this.flush()
            this.onLoad = false
          })
    }
  }
}
</script>

